import React, { Component, Fragment, Suspense } from 'react';
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom';
import Loadable from 'react-loadable';
import Icon from '@mdi/react';
import { mdiGithub, mdiNpm, mdiHandHeart, mdiLinkedin, mdiMenu, mdiGooglePlay, mdiTwitter } from '@mdi/js';
import { Col, Row, Container, Navbar, NavbarBrand, NavbarToggler, Collapse, Nav, NavItem, NavLink, NavbarText } from 'reactstrap';
import * as urls from '../../config.json';
import { routes } from './routes';
import { local_urls } from './urls';

import { HashLink } from 'react-router-hash-link';
import loading_ico from './../../assets/loading.svg';


const loading = () => <Container><div class="loading"><img src={loading_ico} alt="" height="50" width="50" /></div></Container>;

export default class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      json: [],
      value: {},
      isOpen: false
    }
  }

  showMenu = function () {
    this.setState({ isOpen: !this.state.isOpen });
  }.bind(this)

  navigate = function (path) {
    this.props.history.push(path);
  }.bind(this)

  render() {
    return (
      <Fragment>
        <Navbar color="light light-normal text-center" light expand="md" className="bg-white" >
          <Container  className="container-nav">
            <HashLink className="navbar-brand" to="/"><img src="./assets/logo-radovanpranda.png" /></HashLink>
            <button className="navbar-toggler" onClick={this.showMenu} ><Icon path={mdiMenu} size={1.1} /></button>
                
            <Collapse isOpen={this.state.isOpen} navbar>
              <Nav className="mr-auto" navbar>
                <NavItem>
                  <HashLink className="nav-link" to='/#projects'>Projects</HashLink>
                </NavItem>
                <NavItem>
                  <HashLink className="nav-link" to='/#collaboration'>Collaboration</HashLink>
                </NavItem>
                <NavItem>
                  <HashLink className="nav-link" to='/about-me'>About me</HashLink>
                </NavItem>
              </Nav>
              <NavbarText>
                {/*<a href={local_urls["jaspravim"]} className="btn-ico-link btn-npm">
                      {jaspravim}
                    </a>
                    <a href={local_urls["jaudelam"]} className="btn-ico-link btn-npm">
                      {jaspravim}
                    </a>*/}
                <a href={local_urls["linkedin"]} className="btn-ico-link btn-linkedin">
                  <Icon path={mdiLinkedin} size={1.1} />
                </a>
                <a href={local_urls["twitter"]} className="btn-ico-link btn-twitter">
                  <Icon path={mdiTwitter} size={1.1} />
                </a>
                {/*<a href={local_urls["msstore"]} className="btn-ico-link btn-npm">
                      <Icon path={mdiMicrosoftWindows} size={1.1} />
                    </a>*/}
                <a href={local_urls["googleplay"]} className="btn-ico-link btn-googleplay">
                  <Icon path={mdiGooglePlay} size={1.1} />
                </a>
                <a href={urls["github"]} className="btn-ico-link btn-github">
                  <Icon path={mdiGithub} size={1.1} />
                </a>
                <a href={local_urls["npm"]} className="btn-ico-link btn-npm">
                  <Icon path={mdiNpm} size={1.6} />
                </a>
                <HashLink to='/donate' className="btn-ico-link btn-donate">
                  <Icon path={mdiHandHeart} size={1.1} /> <span>Donate</span>
                    </HashLink>
              </NavbarText>
            </Collapse>
          </Container>
        </Navbar>
          <Suspense fallback={loading()}>
            <Switch>
              {routes.map((route, idx) => {
                return route.component ? (
                  <Route
                    key={idx}
                    path={route.path}
                    exact={route.exact}
                    name={route.name}
                    render={props => (
                      <route.component {...props}
                        page_loading={this.props.loading}
                        loading={this.props.loading}
                        processing={this.props.processing}
                        onRedirect={this.navigate}
                      />
                    )} />
                ) : (null);
              })}
            </Switch>
          </Suspense>
        
        <footer>
          <Container>
            <Row>
              <Col sm={6} className="copy-ls">
                <img src='./assets/logo-radovanpranda.png' className="logo-footer navbar-brand disabled mr-0" />
                <div className="copy mt-1">
                  Radovan Pranda &copy; {new Date().getFullYear()}
                </div>
              </Col>
              <Col sm={6} className="copy-rs">
                <div className="copy">
                  Developed, designed and maintained by <a href="https://radovan-pranda.github.io/">Radovan Pranda</a>
                </div>
              </Col>
            </Row>
          </Container>
        </footer>
      </Fragment>
    );
  }
}